@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,400&display=swap');
body{
  background-color: white;
  font-family: 'Open Sans', sans-serif;
}

/*-----------------HEADER----------------*/
.header_container{
  display: flex;  
  height: 75px;
  width: 100vw;
}
.logo_container{
  margin-left: 1.5em;
  width: 25%;
  height: 100%;
}
.links_container{
  display: flex;
  width: 75%;
  height: 100%;
  justify-content: flex-end;
}

.links_container>div{
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-emphasis: none;
  width: 25%;
  height: 100%;
  color:#00796b;
  cursor: pointer;
}
.links_container>div:hover{
  background-color: #00796b;  
  color: white;
}
.text_link{
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 1em;
  color: inherit;
}
.logo{
  margin-top: 1em;  

}
.img{
  width: 55px;
  height: 55px;
}
/*--------------------------------------------------*/
.button{
  display: flex;
  background-color: #00796b;
  cursor: pointer;
  flex-direction: column;
  height: 125px;      
  margin: 25px;
  border: 0px solid #000000;
  margin-top: 35px;
  color: white;
  width: 250px;
  align-items: center;
justify-content: center;
}
.button:hover{
  background-color: white;  
  color: #00796b;
}
@media screen and (min-width: 400px) {
  .button>p{
    text-align: center;
    font-size: 1em;
  }

}
@media screen and (min-width: 700px) {
  .button>p{
    font-size: 1em;
    text-align: center;

  }
  .button>span{
    text-align: center;
  }
}
.button>p{
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  margin-bottom: 0px;
  margin-top: 0px;

}
.body_container{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-left: 2em;
  width: 100vw;
  height: 50vh;
}
.body_text{
  padding-left: 1em;
}
.body_text>h1{
  font-family: 'Roboto', sans-serif;
  font-size: 2.5em;
}
.body_text>p{
  font-family: 'open sans', sans-serif;
  font-size: 1.8em;
}
.buttons_container{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;

}
.button_text_container{
  display: flex;
  flex-direction: column;
  width: 65%;
  margin-top: 5vh;
  height: 100%;
}
.noapp{
  width: 100%;
  height: auto;
  background-color: rgb(75, 75, 75);
  border-left: 10px solid rgb(46, 46, 46) ;
}
.noapp>p{
  color: white;
  font-weight: 800;
  padding: 10px;
}